@import 'mixin';
/* 头部 */
.index-header {
  background: #fff url("../images/index-header-bg.jpg") no-repeat 0 0;
  background-size: 100% auto;
  height: 320px;
  padding: 8px 0 0;
}

.header-address-weather {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 15px;
}

.header-address {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;

  .ico {
    margin-right: 6px;
    padding-top: 2px;

    i {
      display: block;
    }
  }

  .name {
    i {
      color: rgb(255, 255, 255);
      font-size: 14px;
      font-weight: 400;
    }

    a {
      color: rgb(255, 241, 0);
      font-size: 12px;
      font-weight: 500;
      margin-left: 20px;
    }
  }

  .date {
    color: rgb(255, 255, 255);
    font-size: 12px;
    font-weight: 400;
    margin-top: 3px;

    .week {
      margin-left: 10px;
    }
  }
}

.header-weather {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  align-items: center;
  height: 27px;
  background-color: rgba(255, 255, 255,0.2);
  border-radius: 13.5px 0 0 13.5px;
  box-shadow: 0 0.8px 4px rgba(7, 0, 2, 0.2);
  padding: 0 15px;

  img {
    width: 16px;
    display: block;
    margin-right: 5px;
  }

  span {
    color: rgb(255, 255, 255);
    font-size: 12px;
    font-weight: 500;
  }

  .temperature {
    margin-left: 15px;
  }
}

.index-search {
  padding: 0 15px;
  margin-top: 10px;
}

.index-search-box {
  height: 33px;
  background-color: rgb(255, 255, 255);
  border-radius: 6px;
  position: relative;

  input {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    border: none;
    border-radius: 6px;
    height: 33px;
    padding: 5px 10px;
    line-height: 13px;
    font-size: 12px;
    color: #333;
    opacity: 0;
    z-index: 2;
  }

  .tips {
    text-align: center;
    height: 33px;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(145, 145, 145);
    font-size: 12px;
    font-weight: 500;

    i {
      margin-right: 5px;
    }
  }
}

.index-search-box-active {
  input {
    opacity: 1;
  }
}
/* 列表 */
.index-banner {
  position: relative;
  margin-top: -215px;
}

.index-swiper {
  .swiper-slide {
    padding: 0 7.5px;

    img {
      width: 100%;
      display: block;
      border-radius: 5px;
    }
  }
}

.index-pagination {
  position: absolute;
  left: 0;
  bottom: 10px;
  width: 100%;

  .swiper-pagination-bullet {
    width: 8px;
    height: 8px;
    opacity: 0.5;
    background-color: rgb(255, 255, 255);
    box-shadow: 0 1px 4px rgba(228, 64, 31, 0.5);
    margin: 0 3px;
    transition: all 0.1s linear;
  }

  .swiper-pagination-bullet-active {
    background-color: rgb(56, 169, 62);
    opacity: 1;
    box-shadow: none;
  }
}

.index-notice {
  height: 26px;
  left: 0;
  bottom: 0;
  z-index: 9;
  padding: 0 10px;
  overflow: hidden;
  width: 100%;
  margin-top: 6px;
  background: #fff;

  .notice-name {
    float: left;
    width: 18px;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    align-items: center;
    height: 26px;

    img {
      width: 12px;
      display: block;
    }
  }

  .notice-swiper-container {
    float: left;
    height: 26px;
  }

  .txtMarquee-left {
    width: 100%;
    overflow: hidden;
  }

  .txtMarquee-left .listWrap {
    width: 99999px;
    overflow: hidden;
  }

  .txtMarquee-left .list {
    float: left;
  }

  .txtMarquee-left .list li {
    height: 26px;
    line-height: 26px;
    padding: 0 10px;
    float: left;
    color: rgb(94, 94, 94);
    font-size: 12px;
    font-weight: 400;

    a {
      color: rgb(94, 94, 94);
    }
  }

  .txtMarquee-left .list li .dateRight {
    color: #999;
  }
}

.index-nav {
  padding-top: 2px;
  background: #fff;
  padding-bottom: 12px;
}

.nav-swiper {
  .swiper-slide {
    ul {
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      align-items: center;
      flex-wrap: wrap;
    }

    li {
      width: 20%;
      text-align: center;
      margin: 7px 0;

      .pic {
        height: 30px;
        line-height: 30px;

        i {
          vertical-align: middle;
        }
      }

      p {
        color: rgb(94, 94, 94);
        font-size: 12px;
        font-weight: 400;
        margin-top: 2px;
      }
    }
  }
}

.nav-pagination,
.selection-pagination {
  text-align: center;
  margin-top: 3px;

  .swiper-pagination-bullet {
    width: 8px;
    height: 8px;
    background-color: rgb(238, 238, 238);
    margin: 0 3px;
    transition: all 0.1s linear;
    opacity: 1;
  }

  .swiper-pagination-bullet-active {
    background-color: rgb(56, 169, 62);
  }
}

.country-heat {
  background: #fff;
  margin-top: 15px;
  padding: 15px 0 15px 15px;

  .index-headline {
    padding-right: 15px;
  }
}

.index-headline {
  h2 {
    color: rgb(62, 62, 62);
    font-size: 14px;
    font-weight: 500;
    line-height: 1;
  }
}

.country-swiper {
  .swiper-slide {
    margin-top: 10px;

    .pic {
      position: relative;

      img {
        width: 100%;
      }
    }

    .tag {
      position: absolute;
      left: 0;
      bottom: -10px;
      height: 27px;
      background: url("../images/flag04.png") no-repeat center center;
      background-size: 94px 27px;
      z-index: 5;
      width: 100%;
      color: rgb(255, 255, 255);
      font-size: 10px;
      font-weight: 500;
      text-align: center;
      line-height: 18px;
    }

    p {
      color: rgb(94, 94, 94);
      font-size: 11px;
      font-weight: 500;
      text-align: center;
      margin-top: 13px;
    }

    &:nth-child(1) {
      .tag {
        background: url("../images/flag01.png") no-repeat center center;
        background-size: 94px 27px;
      }
    }

    &:nth-child(2) {
      .tag {
        background: url("../images/flag02.png") no-repeat center center;
        background-size: 94px 27px;
      }
    }

    &:nth-child(3) {
      .tag {
        background: url("../images/flag03.png") no-repeat center center;
        background-size: 94px 27px;
      }
    }
  }
}

.rural-statistical {
  background: #fff;
  margin-top: 15px;
  padding: 15px 0 0 15px;
}

.rural-statistical-list {
  .item {
    a {
