    }
  }

  .item-content {
    flex: 1;
    padding-right: 12px;
  }

  .item-title {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .title {
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      align-items: center;

      h3 {
        color: rgb(62, 62, 62);
        font-size: 15px;
        font-weight: 500;
        margin-right: 6px;
      }

      i {
        display: block;
        margin-top: -2px;
      }
