  .item-info {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 12px;
  }

  .item-hot {
    h3 {
      color: rgb(235, 78, 0);
      font-size: 22px;
      font-weight: 400;
      line-height: 26px;
    }

    p {
      color: rgb(145, 145, 145);
      font-size: 11px;
      font-weight: 500;
    }
  }

  .item-authentication {
    ul {
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      align-items: center;
    }

    li {
      margin-left: 10px;
      text-align: center;

      .btn {
        height: 16px;
        background-color: rgba(56, 169, 62, 0.1);
        border: 1px solid #3aab3f;
        border-radius: 8px;
        color: rgb(56, 169, 62);
        font-size: 11px;
        font-weight: 400;
        line-height: 14px;
        text-align: center;
        margin: 5px 0;
        padding: 0 10px;
      }

      .name {
        color: rgb(145, 145, 145);
        font-size: 11px;
        font-weight: 500;
      }
    }
  }
}

.load-more {
  margin-top: 15px;
  // display: -webkit-flex;
  // display: -ms-flex;
  // display: flex;
  // align-items: center;
  // justify-content: center;
  i {
    animation: 1s linear 0s normal none infinite rotate;
    display: inline-block;
    width: 25px;
    height: 25px;
    background: url("../images/loading.png") no-repeat center center;
    background-size: 20px auto;
    margin-right: 5px;
  }

  p {
    text-align: center;
    color: rgb(171, 171, 171);
    font-size: 12px;
    font-weight: 500;
  }

  img {
    display: block;
    margin: 0 auto;
    width: 100px;
    margin-top: 5px;
  }
}
@-webkit-keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
  }
}

.navbar {
  height: 42px;
  background: #38a93e;
