.country-html {
  background: #f6f6f6;

  body {
    background: #f6f6f6;
  }
}

.recommend-country {
  padding: 15px;
  border-bottom: 1px solid #e8e8e8;
  padding-right: 20px;

  h2 {
    color: rgb(62, 62, 62);
    font-size: 14px;
    font-weight: 500;
  }

