.county-grid {
  margin-top: 15px;
  position: relative;

  h2 {
    color: rgb(62, 62, 62);
    font-size: 14px;
    font-weight: 500;
    padding: 0 20px 0 15px;
  }
}

.county-grid-list {
  .group {
    margin-top: 10px;
  }

  h3 {
    height: 22px;
    background-color: rgb(241, 241, 241);
    line-height: 22px;
    color: rgb(145, 145, 145);
    font-size: 12px;
    font-weight: 400;
    padding: 0 20px 0 15px;
  }

  h4 {
    height: 36px;
