    background-color: rgba(56, 169, 62, 0.1);

    &:after {
      content: '';
      width: 7px;
      height: 36px;
      background: url("../images/right02.png") no-repeat right center;
      background-size: 7px auto;
      position: absolute;
      right: 15px;
      top: 0;
    }
  }

  ul {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin: 10px 20px 0 15px;
  }

  li {
    width: calc(25% - 6px);
    height: 36px;
    background-color: rgb(255, 255, 255);
    border: 1px solid rgb(238, 238, 238);
    border-radius: 3px;
    line-height: 34px;
    text-align: center;
    margin: 4.5px 8px 4.5px 0;

    &:nth-child(4n) {
      margin-right: 0;
    }

    a {
      color: rgb(94, 94, 94);
      font-size: 12px;
      font-weight: 500;
      display: block;
    }

    &:hover {
      background-color: rgba(56, 169, 62, 0.1);
      border: 1px solid #39aa3f;

      a {
        color: rgb(56, 169, 62);
      }
    }
  }
}

.county-grid-index {
