      .commit {
        flex: 1;
        font-size: 11px;
        font-weight: 400;
        color: rgb(145, 145, 145);
        line-height: 13px;
        height: 13px;
        overflow: hidden;
        text-overflow: ellipsis;
      }

      .time {
        color: rgb(145, 145, 145);
        font-size: 9px;
        font-weight: 400;
        margin-left: 5px;
      }
    }
  }

  .item-reply-more {
    p {
      font-size: 9px;
      font-weight: 400;
      color: rgb(145, 145, 145);

      a {
        color: rgb(255, 181, 42);
      }
    }
  }
}

.ask-footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background: #f4f4f4;
  z-index: 999;

  ul {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    align-items: center;
    height: 44px;
    background-color: rgba(243, 243, 243, 0.95);
    box-shadow: 0 -1px 0 rgb(232, 232, 232);
  }

  li {
    width: 20%;
    height: 44px;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;

    .ico1 {
      height: 20px;
      width: 40px;
      background: url("../images/ask01.png") no-repeat center center;
      background-size: 16px auto;
    }

    .ico2 {
      height: 20px;
      width: 40px;
      background: url("../images/ask02.png") no-repeat center center;
      background-size: 16px auto;
      position: relative;

      .tag {
        position: absolute;
        left: 50%;
        top: 0;
        width: 6px;
        height: 6px;
        background-color: rgb(255, 72, 0);
        border-radius: 6px;
        margin-left: 4px;
      }
    }

    .ico3 {
      height: 40px;
      width: 40px;
      background: url("../images/ask03.png") no-repeat center center;
      background-size: 40px auto;
    }

    .ico4 {
      height: 20px;
      width: 40px;
      background: url("../images/ask04.png") no-repeat center center;
      background-size: 16px auto;
    }

    .ico5 {
      height: 20px;
      width: 40px;
      background: url("../images/ask05.png") no-repeat center center;
      background-size: 15.5px auto;
    }

    p {
      color: rgb(62, 62, 62);
      font-size: 10px;
      font-weight: 500;
      margin-top: 1px;
    }
  }
}

.user-header {
  background: #38a93e url("../images/user-bg.png") no-repeat center center;
  background-size: cover;
  padding-bottom: 12px;

  h1 {
    color: rgb(255, 255, 255);
    font-size: 16px;
    font-weight: 400;
    text-align: center;
    line-height: 42px;
  }

  .user-logo {
    margin-top: 10px;

    img {
      width: 66px;
      height: 66px;
      border-radius: 66px;
      border: 3px solid rgba(255, 255, 255,0.3);
      display: block;
      margin: 0 auto;
    }

    p {
      text-align: center;
      color: rgb(255, 255, 255);
      font-size: 12px;
      font-weight: 500;
      line-height: 1;
      margin-top: 5px;
    }
  }
}

.user-authentication {
  ul {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    align-items: center;
    height: 33px;
    background-color: rgb(48, 162, 54);
  }

  li {
    flex: 1;
    text-align: center;
    border-right: 1px solid #5eb462;
    height: 33px;
    line-height: 33px;

    a {
      display: block;
    }

    &:last-child {
      border-right: none;
    }

    span {
      color: rgb(176, 238, 180);
      font-size: 10px;
      font-weight: 400;
    }

    i,
    span {}
  }
}

.user-nav {
  margin-top: 15px;

  .item {
    a {
      background: #fff;
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      align-items: center;
      padding-left: 15px;
      height: 44px;
      position: relative;

      &:after {
        content: '';
        position: absolute;
        right: 15px;
        top: 0;
        width: 6px;
        height: 44px;
        background: url("../images/arrow.png") no-repeat center center;
        background-size: 6px auto;
      }

      &:hover {
        opacity: 0.8;
      }
    }
  }

  .ico {
    width: 25px;

    i {
      display: block;
    }
  }

  .content {
    padding-right: 30px;
    border-bottom: 1px solid #e8e8e8;
    flex: 1;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    align-items: center;
    height: 44px;
  }

  .title {
    color: rgb(62, 62, 62);
    font-size: 14px;
    font-weight: 400;
    flex: 1;
  }

  .text {
    color: rgb(171, 171, 171);
    font-size: 11px;
    font-weight: 400;

    span {
      color: rgb(56, 169, 62);
      font-weight: 500;
      padding: 0 3px;
    }
  }

  .msg {
    color: rgb(171, 171, 171);
    font-size: 11px;
    font-weight: 400;
    position: relative;

    .tag {
      position: absolute;
      right: -3px;
      top: 0;
      width: 6px;
      height: 6px;
      background-color: rgb(255, 72, 0);
      border-radius: 6px;
    }
  }

  .list {
    margin-bottom: 15px;

    .item {
      &:last-child {
        .content {
          border-bottom: none;
        }
      }
    }
  }
}
